"use client";

import { useState } from "react";
import PropersyLanding from "./components/landing";
import N8nBidConsole from "./components/N8nBidConsole";

export default function Page() {
  const [currentView, setCurrentView] = useState<"landing" | "console">("landing");

  const handleGetStarted = () => {
    setCurrentView("console");
  };

  const handleBackToLanding = () => {
    setCurrentView("landing");
  };

  if (currentView === "landing") {
    return <PropersyLanding onGetStarted={handleGetStarted} />;
  }

  return (
    <main className="min-h-dvh p-6">
      {/* Back button */}
      <div className="mb-4">
        <button
          onClick={handleBackToLanding}
          className="px-4 py-2 bg-slate-800 text-cyan-400 border border-cyan-400/30 rounded-lg hover:bg-cyan-400 hover:text-slate-900 transition-all duration-300"
        >
          ← Back to Landing
        </button>
      </div>
      
      {/* Fixed proxy, completely not relying on NEXT_PUBLIC_* */}
      <N8nBidConsole defaultWebhookUrl="/api/n8n-proxy" />
      <section className="mt-8 text-xs opacity-60">
      </section>
    </main>
  );
}
