import type React from "react"

interface PropersyLandingProps {
  className?: string
  onGetStarted?: () => void
}

// Simple Button Component
interface ButtonProps {
  children: React.ReactNode
  onClick?: () => void
  size?: "lg"
  variant?: "outline"
  className?: string
  style?: React.CSSProperties
}

function Button({ children, onClick, className, style }: ButtonProps) {
  return (
    <button
      onClick={onClick}
      className={className}
      style={style}
    >
      {children}
    </button>
  )
}

// Simple Card Component
interface CardProps {
  children: React.ReactNode
  className?: string
  style?: React.CSSProperties
}

function Card({ children, className, style }: CardProps) {
  return (
    <div className={className} style={style}>
      {children}
    </div>
  )
}

// Simple utility function
function cn(...classes: (string | undefined)[]) {
  return classes.filter(Boolean).join(' ')
}

// Inline Robot Icon Component
function RobotIcon({ className }: { className?: string }) {
  return (
    <div className={cn("relative", className)}>
      <svg viewBox="0 0 200 200" className="w-full h-full" fill="none" xmlns="http://www.w3.org/2000/svg">
        {/* Robot body */}
        <rect x="60" y="80" width="80" height="90" rx="20" fill="currentColor" className="text-cyan-400" />

        {/* Robot head */}
        <rect x="70" y="40" width="60" height="50" rx="25" fill="currentColor" className="text-cyan-400" />

        {/* Face screen */}
        <rect x="75" y="45" width="50" height="40" rx="20" fill="currentColor" className="text-slate-800" />

        {/* Eyes */}
        <circle cx="88" cy="60" r="4" fill="currentColor" className="text-yellow-400 animate-pulse" />
        <circle cx="112" cy="60" r="4" fill="currentColor" className="text-yellow-400 animate-pulse" />

        {/* Smile */}
        <path d="M 90 70 Q 100 75 110 70" stroke="currentColor" strokeWidth="2" fill="none" className="text-cyan-400" />

        {/* Arms */}
        <rect x="40" y="90" width="25" height="15" rx="7" fill="currentColor" className="text-cyan-400" />
        <rect x="135" y="90" width="25" height="15" rx="7" fill="currentColor" className="text-cyan-400" />

        {/* Hands */}
        <circle cx="35" cy="97" r="8" fill="currentColor" className="text-cyan-400" />
        <circle cx="165" cy="97" r="8" fill="currentColor" className="text-cyan-400" />

        {/* Legs */}
        <rect x="75" y="165" width="15" height="25" rx="7" fill="currentColor" className="text-cyan-400" />
        <rect x="110" y="165" width="15" height="25" rx="7" fill="currentColor" className="text-cyan-400" />

        {/* Feet */}
        <ellipse cx="82" cy="195" rx="12" ry="6" fill="currentColor" className="text-cyan-400" />
        <ellipse cx="118" cy="195" rx="12" ry="6" fill="currentColor" className="text-cyan-400" />

        {/* Body details */}
        <rect x="85" y="100" width="30" height="4" rx="2" fill="currentColor" className="text-purple-400" />
        <rect x="85" y="110" width="30" height="4" rx="2" fill="currentColor" className="text-purple-400" />
        <rect x="85" y="120" width="30" height="4" rx="2" fill="currentColor" className="text-purple-400" />

        {/* Antenna */}
        <line x1="100" y1="40" x2="100" y2="25" stroke="currentColor" strokeWidth="2" className="text-cyan-400" />
        <circle cx="100" cy="22" r="3" fill="currentColor" className="text-cyan-400 animate-pulse" />
      </svg>
    </div>
  )
}

export const PropersyLanding: React.FC<PropersyLandingProps> = ({ className, onGetStarted }) => {
  return (
    <section
      className={cn(
        "relative min-h-screen flex items-center justify-center p-6 overflow-hidden",
        "bg-gradient-to-br from-slate-900 via-blue-900 to-slate-800",
        className,
      )}
    >
      {/* Circuit board background pattern */}
      <div className="absolute inset-0 opacity-10">
        <div
          className="absolute inset-0"
          style={{
            backgroundImage: `
            radial-gradient(circle at 25% 25%, cyan 1px, transparent 1px),
            radial-gradient(circle at 75% 75%, cyan 1px, transparent 1px),
            linear-gradient(90deg, transparent 48%, cyan 49%, cyan 51%, transparent 52%),
            linear-gradient(0deg, transparent 48%, cyan 49%, cyan 51%, transparent 52%)
          `,
            backgroundSize: "50px 50px, 50px 50px, 100px 100px, 100px 100px",
          }}
        />
      </div>

      {/* Background decorative elements */}
      <div className="absolute inset-0 opacity-20">
        <div className="absolute top-20 left-10 w-32 h-32 border border-cyan-400/30 rounded-lg rotate-12"></div>
        <div className="absolute bottom-32 right-16 w-24 h-24 border border-cyan-400/20 rounded-full"></div>
        <div className="absolute top-1/2 left-1/4 w-16 h-16 border border-cyan-400/25 rounded-lg -rotate-45"></div>
        <div className="absolute top-1/3 right-1/3 w-20 h-20 border border-cyan-400/15 rounded-full"></div>
      </div>

      <div className="container mx-auto max-w-7xl relative z-10">
        <div className="grid lg:grid-cols-2 gap-12 items-center">
          {/* Left side - Robot and branding */}
          <div className="flex flex-col items-center lg:items-start space-y-8">
            {/* TASKWEAVERS logo */}
            <div className="flex items-center gap-3 text-cyan-400">
              <div className="w-8 h-8 bg-cyan-400 rounded-lg flex items-center justify-center">
                <div className="w-4 h-4 bg-slate-900 rounded-sm"></div>
              </div>
              <span className="text-lg font-mono tracking-wider">TASKWEAVERS</span>
            </div>

            {/* Main heading */}
            <h1
              className="text-6xl lg:text-8xl font-black text-white tracking-tight"
              style={{
                textShadow: "0 0 20px rgba(34, 211, 238, 0.5), 0 0 40px rgba(34, 211, 238, 0.3)",
              }}
            >
              PROPERSY
            </h1>

            {/* Robot character */}
            <div className="relative">
              <RobotIcon className="w-64 h-64 lg:w-80 lg:h-80" />
              {/* Floating elements around robot */}
              <div className="absolute -top-4 -right-4 w-3 h-3 bg-cyan-400 rounded-full animate-pulse"></div>
              <div className="absolute top-1/2 -left-6 w-2 h-2 bg-cyan-400/70 rounded-full animate-pulse delay-300"></div>
              <div className="absolute -bottom-2 left-1/3 w-4 h-4 bg-cyan-400/50 rounded-full animate-pulse delay-700"></div>
            </div>
          </div>

          {/* Right side - Description card */}
          <div className="flex flex-col space-y-6">
            <Card
              className="p-8 bg-slate-800/80 backdrop-blur-sm border-cyan-400/30 relative"
              style={{
                boxShadow: "0 0 30px rgba(34, 211, 238, 0.2), inset 0 0 30px rgba(34, 211, 238, 0.1)",
              }}
            >
              {/* Card corner decorations */}
              <div className="absolute top-0 left-0 w-6 h-6 border-l-2 border-t-2 border-cyan-400"></div>
              <div className="absolute top-0 right-0 w-6 h-6 border-r-2 border-t-2 border-cyan-400"></div>
              <div className="absolute bottom-0 left-0 w-6 h-6 border-l-2 border-b-2 border-cyan-400"></div>
              <div className="absolute bottom-0 right-0 w-6 h-6 border-r-2 border-b-2 border-cyan-400"></div>

              <div className="space-y-6">
                <p className="text-lg leading-relaxed text-gray-200">
                  A multi-agent bid assistant for European tenders: give it an RFP link or document plus your company&apos;s
                  baseline profile. It extracts key requirements, flags what&apos;s missing, and generates a structured
                  proposal draft. You don&apos;t need to upload existing proposal files; just complete the company form, and
                  the agent will handle the gap analysis and drafting.
                </p>

                <div className="flex flex-col sm:flex-row gap-4">
                  <Button
                    size="lg"
                    onClick={onGetStarted}
                    className="bg-cyan-500 text-slate-900 hover:bg-cyan-400 font-semibold transition-all duration-300"
                    style={{
                      boxShadow: "0 0 20px rgba(34, 211, 238, 0.4)",
                    }}
                  >
                    Get Started
                  </Button>
                  <Button
                    variant="outline"
                    size="lg"
                    className="border-cyan-400 text-cyan-400 hover:bg-cyan-400 hover:text-slate-900 bg-transparent transition-all duration-300"
                  >
                    Learn More
                  </Button>
                </div>
              </div>
            </Card>

            {/* Additional UI elements */}
            <div className="flex items-center gap-4 text-gray-400">
              <div className="flex items-center gap-2">
                <div className="w-2 h-2 bg-cyan-400 rounded-full animate-pulse"></div>
                <span className="text-sm font-mono">AI-POWERED</span>
              </div>
              <div className="flex items-center gap-2">
                <div className="w-2 h-2 bg-cyan-400 rounded-full animate-pulse delay-500"></div>
                <span className="text-sm font-mono">EUROPEAN TENDERS</span>
              </div>
              <div className="flex items-center gap-2">
                <div className="w-2 h-2 bg-cyan-400 rounded-full animate-pulse delay-1000"></div>
                <span className="text-sm font-mono">AUTOMATED</span>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* Corner decorative elements */}
      <div className="absolute top-0 right-0 w-32 h-32 border-r-2 border-t-2 border-cyan-400/20"></div>
      <div className="absolute bottom-0 left-0 w-32 h-32 border-l-2 border-b-2 border-cyan-400/20"></div>

      {/* Additional corner accents */}
      <div className="absolute top-0 right-0 w-16 h-16">
        <div className="absolute top-4 right-4 w-2 h-8 bg-cyan-400/30"></div>
        <div className="absolute top-4 right-4 w-8 h-2 bg-cyan-400/30"></div>
      </div>
      <div className="absolute bottom-0 left-0 w-16 h-16">
        <div className="absolute bottom-4 left-4 w-2 h-8 bg-cyan-400/30"></div>
        <div className="absolute bottom-4 left-4 w-8 h-2 bg-cyan-400/30"></div>
      </div>
    </section>
  )
}

export default PropersyLanding
